<template>    
    <section id="main-content" class="main-content">
        <loader :is-visible="isLoading"></loader>
        <!-- start page heading -->
        <section class="main-content">
            <div class="container-fluid padding-t_10">
                <!-- start left panel -->
                <div class="left-menu-tab ">
                    <div class="col-md-12 p-0 ">
                        <div class="theme-primary partition">
                            <span class="p-name text-white">Manage Client Storage</span>
                        </div>
                    </div>
                    <div class="accordion md-accordion left-filter mt-2" id="accordionEx" role="tablist"
                        aria-multiselectable="true">
                        <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon"
                            asp-action="ReportMain">
                            <input type="hidden" name="reportName" id="reportName" value="" />
                            <input type="hidden" id="hdnmodulename" name="moduleName" value="UserStorageReport" />
                            <div class="card">
                                <div class="card-header filter-head" role="tab" id="headingOne1">
                                    <h5 class="mb-0">
                                        <i class="fa fa-filter" aria-hidden="true"></i> Search<span
                                            class="expand_all_filters">
                                            <span id="Go"><i class="fa fa-search " aria-hidden="true" @click="SearchUserStorage()" ></i></span>
                                            <span id="reset" class="clearsearchtext " @click="ResetButton()" ><i class="fa fa-repeat"
                                                    aria-hidden="true"></i></span>
                                            <span data-target=".multi-collapse" data-toggle="collapse"  @click="toggleExpandCollapseButton(label)"><i
                                                    class="fa fa-sort-desc" aria-hidden="true"></i></span>
                                        </span>
                                    </h5>
                                </div>
                            </div>
                            <div class="card-panel">
                                <div class="card">
                                    <div class="card">
                                        <div class="card-header" role="tab" id="headingOneDepartment">
                                            <h5 class="mb-0">
                                                <a class="collapsed" data-toggle="collapse"
                                                    @click="showByNameDiv = !showByNameDiv" aria-expanded="false"
                                                    aria-controls="collapseThree7">
                                                    <i class="fa fa fa-user" aria-hidden="true" ></i><span> Search by
                                                        Client Name</span><i class="fa fa-angle-down rotate-icon" 
                                                        aria-hidden="true"></i>
                                                </a>
                                                <a href="javascript:;" style="display:none;" id="deptSearch"
                                                    class="tab-Search-btn"><i aria-hidden="true"
                                                        class="fa fa-user"></i></a>
                                            </h5>
                                        </div>
                                        <div class="mb-1 w-100" v-if="showByNameDiv">
                                            <div class="mb-1 w-100">
                                                <div class='input-group customsearchbox'>
                                                    <input class="form-control searchcustomfilter border-right-0" v-model="txtSearchUserName"
                                                        type='text' Placeholder="Search by User Name" />
                                                  
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="card">
                                    <div class="card">
                                        <div class="card-header" role="tab" id="headingOneDepartment">
                                            <h5 class="mb-0">
                                                <a class="collapsed" data-toggle="collapse"
                                                    @click="showByConsumedDiv = !showByConsumedDiv"
                                                    aria-expanded="false" aria-controls="collapseThree7">
                                                    <i class="fa fa-hdd-o" aria-hidden="true"></i><span> Consumed
                                                        Storage</span><i class="fa fa-angle-down rotate-icon"
                                                        aria-hidden="true"></i>
                                                </a>
                                                <a href="javascript:;" style="display:none;" id="deptSearch"
                                                    class="tab-Search-btn"><i aria-hidden="true"
                                                        class="fa fa-user"></i></a>
                                            </h5>
                                        </div>
                                        <div class="mb-1 w-100" v-if="showByConsumedDiv">
                                            <div class="mb-1 w-100">
                                                <div class='input-group customsearchbox'>
                                                    <input class="form-control searchcustomfilter border-right-0" v-model="txtConsumedStorage"
                                                        type='text' Placeholder="Consumed Storage" />
                                                  
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="bottom_filter_button" @click="toggleExpandCollapseButton(label)"><a href="javascript:;" data-target=".multi-collapse"
                            data-toggle="collapse">ExpandAllCollapseAll</a></div>
                </div>
                <div class="right-content">
                    <div class="col-md-12 pb-0">
                        <!-- <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                <div class="progress storagegraph" style="height:2rem;">
                                    <div class="progress-bar progress-bar-primary progress-bar-striped px-2 text-right pb-style" id="divConsumendStorage"><span>x%</span></div>
                                </div>
                            </div>
                        </div> -->
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                               <StorageBar :type="'Doc'"></StorageBar>
                            </div>
                        </div>
                    </div>
                    <hr class="mt-2 mb-2 p-0" />
                    <div class="col-md-12 p-0" v-for="(item,index) in userStorageDetailSummary" :key="index">
                        <!--dashboard-->
                        <ul class="dashbord-widget">
                            <li class="one_wid">
                                <h3><span id="spnStorageLimit" class="inherit-span">{{
                                    item.DOC_STORAGE_MAXLIMIT
                                }}</span> MB<br><span>Total</span></h3>
                                <i class="fa fa-hdd-o" aria-hidden="true"></i>
                            </li>
                            <li class="two_wid">
                                <h3><span id="TotalConsumedSpace" class="inherit-span">{{
                                  totalConsumedStorage
                                }}</span>MB<br><span>Consumed</span></h3>
                                <i class="fa fa-hdd-o" aria-hidden="true"></i>
                            </li>
                            <li class="three_wid">
                                <h3><span id="TotalAvailableSpace" class="inherit-span">{{
                                    avaliablestorage
                                }}</span>MB<br><span>Available</span></h3>
                                <i class="fa fa-hdd-o" aria-hidden="true"></i>
                            </li>
                            <li class="four_wid">
                                <h3><span id="AssignedtoUser" class="inherit-span">{{totalUserAllowedStorage}}</span>
                                    MB<br><span>Assigned To User</span></h3>
                                <i class="fa fa-user-o" aria-hidden="true"></i>
                            </li>
                            <li class="five_wid">
                                <h3><span class="inherit-span" id="abc">{{ AllowedClientStorage }}</span>
                                    MB<br><span>Assigned To Client</span>
                                </h3>
                                <i class="fa fa-id-card-o" aria-hidden="true"></i>
                            </li>
                        </ul>
                        <!--dashboard-->
                        <div class="clearfix"></div>
                    </div>
                    <div class="col-md-12 p-0">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-actions float-right">
                                <a id="btnStorageSpace" class="p-action-btn text-white disabled" href="#myModal"
                                    @click="AllocatedSpacePopup" data-toggle="modal" alt="Allocate Space"
                                    title="Allocate Space">
                                    <i class="fa fa-plus"></i><br />Allocate Space
                                </a>
                                <!-- <RouterLink to="/AdvancedSetup/Detail" class="p-action-btn text-white" href="#myModal"
                                    data-toggle="modal" alt="Allocate Space"
                                    title="Allocate Space">
                                    <i class="fa fa-angle-double-left"></i><br />Back    
                                </RouterLink> -->
                                <input id="ReportViewer" type="hidden" value="L" />
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns"><i
                                        class="fa fa-angle-up text-white"></i></a>
                            </span>
                        </div>
                    </div>
                    <div class="col-md-12 p-0">
                        <div class="listing mt-2">
                            <div class="expand-list">
                                <a href="javascript:;" onclick="AddExpandCollapse(this)"
                                    class="expand-list-btn justify-content-center align-items-center">
                                    <i class="icon-solid arrow-solid-left" style="font-size:9pt"></i>
                                </a>
                            </div>

                            <!-- Listing record of user storage -->
                            <div class="listing py-3 px-3">
                                <div class="table-responsive table-fix-header" id="div_forListView">
                                    <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th style="width:50px !important;" class="text-center"><input
                                                        id="chkAll" class="chkitems" value="" type="checkbox"
                                                        v-on:change="onChange()"></th>
                                                <th>
                                                    <span>Client Name</span>
                                                </th>
                                               
                                                <th>
                                                    <span>Email</span>
                                                </th>
                                                <th>
                                                    <span>Status</span>
                                                </th>
                                                <th>
                                                    <span>Consumed Stoarage (MB)</span>
                                                </th>
                                                <th>
                                                    <span>Allocated Storage (MB)</span>
                                                </th>
                                                <th data-width="5%" class="text-center nonresize">
                                                    <span>Action</span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,UserDataIndex) in UserData" :key="UserDataIndex">
                                                <td class="text-center"><input :Id="item.USER_ID"
                                                        v-bind:value="item.USER_ID" class="chkItems" type="checkbox"
                                                        v-on:change="onChange()"></td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.USER_NAME }}
                                                    </span>
                                                </td>
                                               
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.EMAIL_ID }}
                                                    </span>
                                                </td>
                                                <td>                                
                                                            <span class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-success" v-if="item.STATUS_ID  == '1001'"> Active</span>

                                                            <span title="" class="sent bg-secondary btn d-inline-block w-80 text-center text-truncate text-white" v-if="item.STATUS_ID == '1084'"> Approved</span>
                                                      
                                                            <span title="" class="stop btn d-inline-block w-80 text-center text-truncate btn-danger"  v-if="item.STATUS_ID == 'Rejected'"> Rejected</span>
                                                        
                                                            <span title="" class="stop px-2 bg-primary btn d-inline-block w-80 text-center text-truncate" v-if="item.STATUS_ID == '1003'"> Deleted</span>
                                                        
                                                            <span class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-danger" v-if="item.STATUS_ID == '1002'">Inactive</span>
                                                        
                                                            <span class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-secondary" v-if="item.STATUS_ID == '1005'"> Pending</span>
                                                        
                                                            <span class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-warning" v-if="item.STATUS_ID == 'ActivationAwaited'"> ActivationAwaited</span>
                                                </td>
                                                <td>
                                                    <span class="text-right text-overflow-dynamic-container">
                                                        {{ item.CONSUMED_STROAGE }}
                                                    </span>
                                                </td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        <input type="text" asp-for="item.MaxStorage"
                                                            :id="'idMaxStorage' + item.USER_ID" step="0.01"
                                                            maxlength="13" v-model="item.ALLOWED_STORAGE"
                                                            class="maxAllowedStorage  form-control text-right disabled" />
                                                        <!-- :value="item.ALLOWED_STORAGE" -->
                                                    </span>
                                                </td>
                                                <td class="text-center single-action">

                                                    <a class="aEdit" title="Edit" v-if="hideEditIcon"
                                                        :id="'idEdit' + item.USER_ID"
                                                        @click="editAllowedStorage(item.USER_ID)"><i
                                                            class="fa fa-pencil text-success action-icon "></i></a>
                                                    <div :id="'divAction' + item.USER_ID" style="display: none;">

                                                        <a class="aCancel mr-2" :id="'idCancel_' + item.USER_ID"
                                                            :value="item.USER_ID" title="Cancel"
                                                            @click="CancelAction(item.USER_ID, item.ALLOWED_STORAGE)"
                                                            href="javascript:;"><i
                                                                class="fa fa-times-circle text-danger action-icon"></i></a>
                                                        <a class="aSave" title="Save" :value="item.USER_ID"
                                                            :id="'idSave_' + item.USER_ID" href="javascript:;"
                                                            @click="SaveData(item.USER_ID, item.ALLOWED_STORAGE)"><i
                                                                class="fa fa-save text-primary action-icon"></i></a>
                                                    </div>
                                                </td>
                                            </tr>
                                            <tr v-if="NoRecordLength == 0">
                                                <td  colspan="7"
                                                    class="text-danger text-center no-record">No Record Found</td>
                                            </tr>
                                            <tr v-if="NoRecordLength > 0">
                                        <td colspan="2" class="text-right fr font-weight-bold">Total</td>
                                        <td  colspan="4" class="text-right fr font-weight-bold">{{ totalConsumedStorage }} MB </td>
                                        <td  class=" text-right fr font-weight-bold">{{ totalAllowedStorage }} MB</td>
                                    </tr>
                                        </tbody>
                                    </table>
                                    <!-- <Pager v-bind:numberofrecords="totalRecords" prev-text="Prev"
                                        v-on:change="pageSizecommon" first-text="First" last-text="Last"
                                        next-text="Next" @click="pagerMethod"></Pager> -->
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <AllocatedSpace :jsonobj="jsonobj" v-if="isAllocatedSpaceOpen" ></AllocatedSpace>
    </section>
</template>
<script>
import Pager from '../../components/Common/Pager.vue';
import DataService from '../../services/DataService';
import AllocatedSpace from '../UI/Modal/ModalAllocatedSpace.vue'
import StorageBar from '../../components/Common/StorageBar.vue'
export default {
    components: {
        Pager,
        AllocatedSpace,
        StorageBar
    },
    data() {
        return {
            isLoading: false,
            UserData: [],
            DepartmentData: [],
            designationData: [],
            userStorageDetailSummary: [],
            pageNo: 1,
            searchCondition: null,
            sortExp: null,
            sortBy: null,
            pageSize: 10,
            totalRecords: '',
            dropDivShow: false,
            showByNameDiv: false,
            showByConsumedDiv: false,
            divDesignationShow: false,
            hideEditIcon: true,
            isAllocatedSpaceOpen: false,
            UserStorageValue: null,
            UserId: null,
            saveStorage: null,
            StorageValue: null,
            jsonobj: [],
            JSONSearchData:[],
            departmentList: null,
            designationList: null,
            txtSearchUserName: "",
            txtConsumedStorage: "",
            userFilterData: [],
            CheckIfExist: [],
            jsonFilteredData:[],
            label:false,
            totalConsumedStorage: 0,
            totalAllowedStorage:0,
            NoRecordLength : 0,
            AllowedClientStorage:null,
            totalUserAllowedStorage:null
        }
    },

    created() {
       
        var vm = this;
        vm.FetchData();
        vm.FetchUserData();
        vm.GetDDLDesignationData();
        vm.GetDDLDepartmentData();


        
    }, 
    

    methods:
    {

         SearchUserStorage: function() {
            var vm = this;
            $('.chkItems:checked').each(function () {
                var deptId = $(this).val();
                // vm.CheckIfExist =  vm.UserData.filter(function (el) {
                //     return el.DEPARTMENT_ID == deptId;
                // });
                vm.CheckIfExist =  vm.UserData.filter(dept=>dept.DEPARTMENT_ID==deptId)
                vm.JSONSearchData= Array.from(vm.CheckIfExist);
                vm.userFilterData=vm.userFilterData.concat(vm.JSONSearchData)
            });
            
            $('.chkItems:checked').each(function () 
            {
                if(vm.userFilterData.length == 0)
                {
                    vm.jsonFilteredData=vm.UserData;
                }
                var designId = $(this).val();
            //     vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
            //     return el.DESIGNATION_ID == designId;
            // });        
            vm.CheckIfExist =  vm.UserData.filter(desig=>desig.DESIGNATION_ID==designId);       
            vm.JSONSearchData= Array.from(vm.CheckIfExist);
            vm.userFilterData=vm.userFilterData.concat(vm.JSONSearchData)
            });
            if(vm.txtSearchUserName!= ""){
                if(vm.userFilterData.length == 0)
                {
                    vm.jsonFilteredData=vm.UserData;
                }
                vm.CheckIfExist =  vm.UserData.filter(usr=>usr.USER_NAME.toLowerCase().includes(vm.txtSearchUserName)) ;
                // vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
                //     return el.USER_NAME == vm.txtSearchUserName;
                // });
                vm.JSONSearchData= Array.from(vm.CheckIfExist);
            vm.userFilterData=vm.userFilterData.concat(vm.JSONSearchData)
            }
            if(vm.txtConsumedStorage != ""){
                if(vm.userFilterData.length == 0)
                {
                    vm.jsonFilteredData=vm.UserData;
                }
                vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
                    return el.CONSUMED_STROAGE == vm.txtConsumedStorage;
                });
                vm.JSONSearchData= Array.from(vm.CheckIfExist);
            vm.userFilterData=vm.userFilterData.concat(vm.JSONSearchData)
            }
            vm.UserData = vm.userFilterData;
            vm.getTotalStorageData();
        },
      

        ResetButton: function(){
            var vm=this;
            vm.FetchData();
            vm.GetDDLDesignationData();
              vm.GetDDLDepartmentData();
              vm.dropDivShow=false;
              vm.divDesignationShow=false;
              vm.showByNameDiv=false;
              vm.showByConsumedDiv=false;
              $('.chkItems').prop("checked", false);
              vm.txtSearchUserName='';
              vm.txtConsumedStorage='';
              vm.toggleExpandCollapseButton(vm.label=true);
        },

        toggleExpandCollapseButton: function(){
            var vm=this;
            if(vm.label==false)
            {
                vm.dropDivShow=true;
            vm.divDesignationShow=true;
            vm.showByNameDiv=true;
            vm.showByConsumedDiv=true;
            vm.label=true;
            }
            else{
                vm.dropDivShow=false;
            vm.divDesignationShow=false;
            vm.showByNameDiv=false;
            vm.showByConsumedDiv=false;
            vm.label=false;
            }
            //vm.txtSearchUserName= "";
            //vm.txtConsumedStorage= "";

        },
        
        getTotalStorageData: function(){
            debugger
            var vm = this;
            vm.totalAllowedStorage = 0;
            vm.totalConsumedStorage = 0;
          vm.UserData.forEach(x => {
                vm.totalAllowedStorage += x.ALLOWED_STORAGE;
                vm.totalConsumedStorage += x.CONSUMED_STROAGE;
            });
            vm.avaliablestorage=parseInt(vm.totalAllowedStorage)-parseInt(vm.totalConsumedStorage);
           
        },

        AllocatedSpacePopup: function () {
             debugger
             var vm = this;
             vm.jsonobj=[];
            var values = ''
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                 debugger
                values = $(this).val();
                let objData = {
                    Id: values
                }
                vm.jsonobj.push(objData)
            })
            vm.isAllocatedSpaceOpen = true;
        },

        closeAllocatedSpace: function () {
            var vm = this;
            vm.isAllocatedSpaceOpen = false;
        },

        // Save data on save icon 
        SaveData: function (id, allowed) {
             
            var vm = this
            vm.isLoading = true;
                vm.UserId = id,
                vm.UserStorageValue = allowed;
            let objData = [{

                DataId: vm.UserId,
                DataValue: vm.UserStorageValue
            }]

            var jsonObj = JSON.stringify(objData)
            var params = `jsonData=${jsonObj}&refCode=UserSetting&moduleName=DocStorage`;
            DataService.ManageDocUserStorageSettingData(params).then((response) => {
                 
                if (response.data == 0) {
                    this.ShowAlert("Storage Configuration Data Updated Successfully")
                    $('#idMaxStorage' + id).addClass('disabled');
                    $('#divAction' + id).hide();
                    $('#idEdit' + id).show();
                    vm.FetchData();
                    vm.GetDDLDesignationData();
                    vm.GetDDLDepartmentData();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })
            vm.isLoading = false;
        },

        CancelAction: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).addClass('disabled');
            $('#idEdit' + id).show();
            $('#divAction' + id).hide();
            vm.FetchData();
            vm.GetDDLDesignationData();
            vm.GetDDLDepartmentData();
        },

        editAllowedStorage: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).removeClass('disabled');
            $('#idEdit' + id).hide();
            $('#divAction' + id).show();
          
        },
        //Pagination
        PagerData: function (PageNo, sortBy, sortExp, pageSize) {
            // 
            var vm = this;
            vm.pageNo = PageNo;
            vm.pageSize = pageSize;
            vm.FetchData(vm.pageNo, sortBy, sortExp, vm.pageSize);
        },
        // To pass variable 
        pagerMethod: function (e) {
             ;
            if (e.target.innerHTML == 'Prev') {
                this.pageNo = this.pageNo - 1;
            }
            else if (e.target.innerHTML == 'Next') {
                this.pageNo = parseInt(this.pageNo) + 1;
            }
            else {
                this.pageNo = e.target.innerHTML;
            }
            if (this.pageNo == "First") {
                this.pageNo = "1";
            }
            if (this.pageNo == "Last") {
                this.pageNo = this.TotalPages
            }
            if (parseInt(this.pageNo) > 0) {
                this.PagerData(this.pageNo, '', '', this.pageSize)
            }
            else {
                this.pageNo = 1
            }
        },
        pageSizecommon: function (e) {
             
            var vm = this;
            vm.pageNo = 1;
            vm.pageSize = e.target.value
            vm.PagerData(vm.pageNo, '', '', vm.pageSize)
        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#btnStorageSpace').removeClass('disabled');
            }
            else {
                $('#btnStorageSpace').addClass('disabled');
            }
        },
        GetDDLDepartmentData: function () {
             
            var vm = this;
            let params = {};
            params["type"] = "DOC_TAG";
            var param = "moduleName=DocStorage&type=DEPARTMENT"
            DataService.GetDDLDatas(param).then((response) => {
                 
                vm.DepartmentData = response.data.DATA;
                // vm.DepartmentData.forEach((item, index) => {
                //     vm.Value = item.value;
                //     vm.first_value = item.name
                //     vm.Options.push({
                //         value: vm.Value,
                //         label: vm.first_value
                //     })
                // });
            })
        },
        GetDDLDesignationData: function () {
             
            var vm = this;
            let params = {};
            params["type"] = "DOC_TAG";
            var param = "moduleName=DocStorage&type=HR_DESIG"
            DataService.GetDDLDatas(param).then((response) => {
                 
                vm.designationData = response.data.DATA;
                // vm.DepartmentData.forEach((item, index) => {
                //     vm.Value = item.value;
                //     vm.first_value = item.name
                //     vm.Options.push({
                //         value: vm.Value,
                //         label: vm.first_value
                //     })
                // });
            })
        },
        FetchUserData: function () {
           // debugger
            var vm = this;
            vm.isLoading = true;
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=1,2,3`;
            DataService.GetUserStorageDetail(params).then(function (response) {

                vm.UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                vm.UserData.forEach(x => {
                vm.totalUserAllowedStorage += x.ALLOWED_STORAGE;
                vm.totalUserConsumedStorage += x.CONSUMED_STROAGE;
            });
            })
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            $('#btnStorageSpace').addClass('disabled')

        },
        FetchData: function () {
             debugger
            var vm = this;
            vm.isLoading = true;
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=4`;
            DataService.GetUserStorageDetail(params).then(function (response) {
                 
                vm.UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                vm.totalRecords = vm.UserData[0].totalRecords
                vm.userStorageDetailSummary = JSON.parse(response.data).USERSTORAGEDETAILSUMMARY;
                vm.UserData.forEach(x => {
                    if(x.ALLOWED_STORAGE==null||x.ALLOWED_STORAGE==undefined){
                        x.ALLOWED_STORAGE=0;
                    vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                    }
                    else{
                        vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                    }
                })
     
     // Calculation the sum using forEach
    vm.getTotalStorageData();
                vm.isLoading = false;
                if(vm.UserData==null){
                    vm.NoRecordLength=0;
                }
                else{
                    vm.NoRecordLength = vm.UserData.length;
                }
            })
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
           $('#btnStorageSpace').addClass('disabled')

        },
    }
}

</script>

