<template>
   
    <div class="modal-open my-popups" style="z-index:9999998">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block " tabindex="-1" role="dialog">
            <div class="modal-dialog" style="width:35%">
                <div class="modal-content">
                    <div class="modal-header ">
                        <h4 name="header" class="modal-title">Allocate Space</h4>
                        <button @click="ClosePopup" class="close"><em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form v-slot="{ valid,errors }" ref="form">
                            <section id="divMainContent" class="main-content">
                                <div class="row">
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>Allocate Space (MB): <span class="text-danger">*</span></label>
                                            <Field  :rules="{ 'required': true }" name="Allocate Space"
                                                v-slot="{ errors }">
                                                <input  id="allocateSpace"
                                                    :class="{ 'form-control': true, 'is-invalid':errors[0]}"
                                                    type="text" maxlength="200" v-model="allocatedSpaceValue" />
                                                <span name="allocateSpace" id="allocateSpace"
                                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                        <div class="col-lg-25">
                                            <div><span class="mandatory">{{ $t("MandatoryString") }}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </Form>
                        <div class="text-right pr-0 pb-0">
                            <button @click="SaveData()" title="Save"
                                class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em aria-hidden="true"
                                    class="fa fa-save pr-2"></em> Save</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import DataService from '../../../services/DataService'
export default {

    data: function() {
        return{
        isLoading: false,
        allocatedSpaceValue: 0,
        isAllocatedSpaceOpen: false,
        UserStorageValue: null,
        jsonObjData: [],
        SumOfAllowedStorage:null,
        AllocatedStorage:null,
        AvailableSpace:null
    }
    },
    props:
    {
        jsonobj: {
            type: Array,

        }
    },

    created() {
        var vm =this;
        if(vm.$parent.$refs.Client){
        vm.SumOfAllowedStorage= vm.$parent.$refs.Client.totalAllowedStorage 
        vm.AllocatedStorage=vm.$parent.$refs.Client.AllowedClientStorage 
        vm.AvailableSpace=vm.$parent.$refs.Client.AvailableSpace 
        }
    if(vm.$parent.$refs.User){
        vm.SumOfAllowedStorage= vm.$parent.$refs.User.totalAllowedStorage 
        vm.AllocatedStorage=vm.$parent.$refs.User.AllowedClientStorage 
        vm.AvailableSpace=vm.$parent.$refs.User.AvailableSpace 
    }
    },

    methods: {
       
        SaveData: function () {
            debugger;
            var vm = this
            vm.isLoading = true;
            var oldValue = 0;
            // vm.$refs.form.setFieldTouched('allocateSpace', true);
            vm.UserStorageValue = null;
            vm.jsonObjData = [];
            let userSetting={};
            if(isNaN(vm.allocatedSpaceValue)){
                vm.ShowAlert(vm.$t("Allocate Storage must be numeric."), "failure", true, vm.$t('Alert'));
            }
            else{
                vm.UserStorageValue = vm.allocatedSpaceValue;
                var count=vm.jsonobj.length;
                    for (var i = 0; i < vm.jsonobj.length; i++) {
                        oldValue += parseInt(vm.jsonobj[i].value);
                    }
                vm.SumOfAllowedStorage=vm.SumOfAllowedStorage-oldValue;
                vm.SumOfAllowedStorage=vm.SumOfAllowedStorage+(parseInt(vm.UserStorageValue)*count);
                var totalAvaialableStorage=vm.AllocatedStorage+vm.AvailableSpace;
                
                if (vm.jsonobj != null) {
                    vm.jsonobj.forEach((item, index) => {
                        let objData = {
                            DataId: item.Id,
                            DataValue: vm.UserStorageValue,
                        }
                        
                        vm.jsonObjData.push(objData)
                    
                    });
                    userSetting=[{
                        refCode: 'UserSetting',
                        moduleName: 'DocStorage',
                    }]
                    //vm.jsonObjData.push(userSetting);
                }
                else {
                    let objData = {
                        DataId: vm.UserId,
                        DataValue: vm.UserStorageValue,
                    }
                    
                    vm.jsonObjData.push(objData)
                
                }
                var configData="configData"
                var conditionData="conditionData"
                var params="[{"+JSON.stringify(configData)+":"+JSON.stringify(vm.jsonObjData)+", "+JSON.stringify(conditionData)+":"+JSON.stringify(userSetting)+"}]"
                if(totalAvaialableStorage<vm.SumOfAllowedStorage)
                {
                    vm.$parent.closeAllocatedSpace();
                    vm.ShowAlert(vm.$t("You cannot assign Storage more than available Storage"), "failure", true, vm.$t("Alert"));
                }
                else{
                var ParentData = JSON.stringify(vm.jsonObjData);
                vm.$refs.form.validate().then((result) => {
                    if (result != false) {
                        DataService.ManageDocUserStorageSettingData(JSON.stringify(params)).then((response) => {
                            if (response.data == 0) {
                                this.ShowAlert("Allocated Space Updated Successfully")
                                vm.$parent.closeAllocatedSpace();
                                vm.$emit("FetchDatas");
                                // vm.$parent.GetDDLDepartmentData();
                                // vm.$parent.GetDDLDesignationData();
                                vm.jsonObjData = [];
                            }
                            else {
                                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                            }
                        })
                    }

                });

                vm.isLoading = false;
                }
            }
        },
        ClosePopup: function () {
            var vm = this;
            vm.isLoading = true,
            vm.$emit('closeAllocatedSpace');
            vm.$parent.closeAllocatedSpace();
            vm.$parent.FetchDatas();
            vm.$parent.GetDDLDepartmentData();
            vm.$parent.GetDDLDesignationData();
            vm.isLoading = false;
        },



    }
}

</script>